
import Tabbar from '@/components/Public/Tabbar'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'

export default {
components: {
Tabbar, Swiper, Cell, Group, Radio, Badge, Flexbox, FlexboxItem, Divider
},
data: () => ({
list: [],
noMore: false,
isEmpty: false,
page: 1,
pageSize: 20,
img_list: [{
url: '',
img: 'http://thumb.niutuku.com/960x1/8f/b8/8fb8fb2623afa6336e2be205718f5f0e.jpg',
title: ''
}, {
url: '',
img: 'http://thumb.niutuku.com/960x1/94/73/94731f3222b54c0fac39327cec4b438e.jpg',
title: ''
}, {
url: '',
img: 'https://gss3.bdstatic.com/7Po3dSag_xI4khGkpoWK1HF6hhy/baike/c0%3Dbaike116%2C5%2C5%2C116%2C38/sign=e30f83e88ab1cb132a643441bc3d3d2b/94cad1c8a786c917a899ba3ece3d70cf3bc7571b.jpg',
title: ''
}, {
url: '',
img: 'http://thumb.niutuku.com/960x1/da/35/da357021b4d95225d374c86f14fec6a8.jpg',
title: ''
}]
}),
mounted () {
this.getData()
},
methods: {
loadMore: function () {
if (this.list.length === 0) {
return
}
if (this.noMore) {
return
}
this.busy = true
setTimeout(() => {
this.getData()
this.busy = false
}, 1500)
},
getData: function () {
this.$getData(this.$configs.api.article, `?page=${this.page}&pageSize=${this.pageSize}`, response => {
const list = response.list
if (JSON.stringify(list) === '[]') {
if (this.list.length === 0) {
this.isEmpty = true
return
} else {
this.noMore = true
return
}
} else {
this.list = this.list.concat(list)
this.page++
}
})
}
}
}



